import { fetchConToken, fetchSinToken } from "../helpers/fetch";

export const startLogin = (email, password) => {
	return async (dispatch) => {
		const resp = await fetchSinToken("api/usuarios/login", { email, password }, "POST");

		const body = await resp.json();

		if (body.message) {
			dispatch(authError(body.message));
		}

		if (body.token) {
			localStorage.setItem("token", body.token);
			localStorage.setItem("token-init-date", new Date().getTime());

			dispatch(
				login({
					uid: body.usuario.usuarioID,
					email: body.usuario.email,
					role: body.usuario.role,
				})
			);
		}
	};
};

export const login = (user) => ({
	type: "AUTH_LOGIN",
	payload: user,
});

export const startLogout = () => {
	return (dispatch) => {
		localStorage.clear();
		dispatch(logout());
	};
};

export const logout = () => ({
	type: "AUTH_LOGOUT",
});

export const authError = (payload) => ({
	type: "AUTH_ERROR",
	payload,
});

/// SIGN UP

export const signAccount = (first_name, last_name, email, password) => {
	return async (dispatch) => {
		const resp = await fetchSinToken(
			"api/usuarios/signup",
			{ email, password, last_name, first_name },
			"POST"
		);
		const body = await resp.json();
		// console.log(body.errors);
		if (body.errors) {
			dispatch(signError(body.errors));
			return;
		}

		dispatch(signMessage(body));
	};
};

export const signMessage = (payload) => ({
	type: "SIGN_MESSAGE",
	payload,
});

export const signError = (payload) => ({
	type: "SIGN_ERROR",
	payload,
});

export const startChecking = () => {
	return async (dispatch) => {
		const resp = await fetchConToken("api/usuarios/login/renew");

		const body = await resp.json();

		if (body.message) {
			dispatch(checkingFinish());
		}

		if (body.token) {
			localStorage.setItem("token", body.token);
			localStorage.setItem("token-init-date", new Date().getTime());

			dispatch(
				login({
					uid: body.payload.usuarioID,
					email: body.payload.email,
					role: body.payload.role,
				})
			);
		} else {
			dispatch(checkingFinish());
		}
	};
};

const checkingFinish = () => ({
	type: "CHECKING_FINISH",
});
