import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import { pink, red } from "@material-ui/core/colors";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import { useDispatch, useSelector } from "react-redux";
import { Button, CardActions, Collapse, Divider } from "@material-ui/core";

import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import { borrarPostyRequest } from "../actions/posts";

const useStyles = makeStyles((theme) => ({
	root: {
		maxWidth: 345,
	},
	media: {
		height: 0,
		paddingTop: "56.25%", // 16:9
	},
	expand: {
		transform: "rotate(0deg)",
		marginLeft: "auto",
		transition: theme.transitions.create("transform", {
			duration: theme.transitions.duration.shortest,
		}),
	},
	expandOpen: {
		transform: "rotate(180deg)",
	},
	avatar: {
		backgroundColor: red[500],
	},
	pink: {
		color: theme.palette.getContrastText(pink[500]),
		backgroundColor: pink[500],
	},
}));

export default function CardComponent({
	description,
	createdAt,
	jobTitle,
	firstName,
	lastName,
	email,
	requirements,
	type,
	id,
	setborrarRefresh,
}) {
	const auth = useSelector((state) => state.auth);

	const classes = useStyles();
	const [expanded, setExpanded] = React.useState(false);
	const [anchorEl, setAnchorEl] = React.useState(null);

	const dispatch = useDispatch();

	const handleExpandClick = () => {
		setExpanded(!expanded);
	};

	const handleClick = (event) => {
		setAnchorEl(event.currentTarget);
	};

	const endpoint = type === "post" ? `api/removepost/${id}` : `api/removerequest/${id}`;
	const handleDelete = () => {
		setAnchorEl(null);
		setborrarRefresh(id);
		dispatch(borrarPostyRequest(endpoint));
	};

	return (
		<Card className={classes.root}>
			<CardHeader
				avatar={
					<Avatar aria-label="recipe" className={classes.avatar}>
						{firstName[0]}
					</Avatar>
				}
				action={
					auth.role === "ADMIN_ROLE" || auth.email === email ? (
						<div>
							<IconButton
								aria-controls="simple-menu"
								aria-haspopup="true"
								onClick={handleClick}
							>
								<MoreVertIcon />
							</IconButton>
							<Menu
								id="simple-menu"
								anchorEl={anchorEl}
								keepMounted
								open={Boolean(anchorEl)}
								onClose={handleDelete}
							>
								<MenuItem onClick={handleDelete}>Borrar Post</MenuItem>
							</Menu>
						</div>
					) : null
				}
				title={jobTitle}
				subheader={createdAt}
			/>

			<CardContent>
				<Typography variant="body1" color="textPrimary" component="p">
					{description}
				</Typography>
				<br />
				<Typography variant="body1" color="textPrimary" component="p">
					{requirements}
				</Typography>
			</CardContent>

			<CardActions disableSpacing>
				<Button
					variant="contained"
					color="secondary"
					onClick={handleExpandClick}
					aria-expanded={expanded}
				>
					Más Info
				</Button>
			</CardActions>
			<Collapse in={expanded} timeout="auto" unmountOnExit>
				<Divider />
				<CardContent>
					<Typography paragraph>{`Creado por: ${firstName} ${lastName}`}</Typography>
					<Typography paragraph>{`Email: ${email}`}</Typography>
				</CardContent>
			</Collapse>
		</Card>
	);
}
