import React, { useEffect } from "react";
import {
	Box,
	Button,
	Divider,
	Grid,
	makeStyles,
	Paper,
	TextField,
	Typography,
} from "@material-ui/core";
import loginImage from "../assets/login_image.svg";
import { useForm } from "../hooks/useForm";
import { startLogin } from "../actions/auth";
import { useDispatch, useSelector } from "react-redux";

import Alert from "@material-ui/lab/Alert";
import { useHistory } from "react-router";

const useStyles = makeStyles({
	PaperClass: {
		width: 300,
		height: 550,
		display: "flex",
	},
	iconClass: {
		padding: 15,
		width: 180,
	},
	formClass: {
		display: "flex",
		flexDirection: "column",
		alignItems: "center",
	},
});

const LoginScreen = () => {
	const [formLoginValues, handleLoginInputChange] = useForm();
	const classes = useStyles();

	const dispatch = useDispatch();

	const handleLogin = (e) => {
		e.preventDefault();
		console.log(formLoginValues);
		dispatch(startLogin(formLoginValues.lEmail, formLoginValues.lPassword));
	};

	const state = useSelector((state) => state.auth);

	const history = useHistory();

	useEffect(() => {}, [state]);

	return (
		<Box
			p={0}
			className={classes.boxContainerClass}
			bgcolor="primary.main"
			style={{ height: "100vh" }}
			justifyContent="center"
			alignItems="center"
			display="flex"
		>
			<Paper elevation={3} className={classes.PaperClass}>
				<Grid container direction="column" justify="space-evenly" alignItems="center">
					<Box
						justifyContent="center"
						alignItems="center"
						display="flex"
						flexDirection="column"
					>
						<img className={classes.iconClass} src={loginImage} alt="login screen" />
						<Button
							variant="contained"
							color="primary"
							onClick={() => {
								history.push("/signup");
							}}
						>
							Crear una cuenta
						</Button>
					</Box>

					<Divider />

					<Box
						justifyContent="center"
						alignItems="center"
						display="flex"
						flexDirection="column"
					>
						<Typography variant="h5">Login</Typography>

						<form className={classes.formClass} onSubmit={handleLogin}>
							<TextField
								autoComplete="off"
								margin="normal"
								id="outlined-basic"
								label="Email"
								type="Email"
								variant="outlined"
								name="lEmail"
								onChange={handleLoginInputChange}
								required
							/>
							<TextField
								autoComplete="off"
								margin="normal"
								id="outlined-basic"
								label="Password"
								variant="outlined"
								name="lPassword"
								onChange={handleLoginInputChange}
								required
							/>
							<br />
							{state.message !== null ? (
								<Alert severity="error">{state.message}</Alert>
							) : null}
							<br />
							<Button type="submit" variant="contained" color="primary">
								Login
							</Button>
						</form>
					</Box>
				</Grid>
			</Paper>
		</Box>
	);
};

export default LoginScreen;
