import { Box, Button, makeStyles, Paper, TextField, Typography } from "@material-ui/core";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { logout, signAccount } from "../actions/auth";
import { useForm } from "../hooks/useForm";

import Alert from "@material-ui/lab/Alert";
import { useHistory } from "react-router";

const useStyles = makeStyles({
	paperContainer_signup: {
		display: "flex",
		flexDirection: "column",
		alignItems: "center",
		justifyContent: "center",
		width: 300,
		height: 650,
	},
	formClass: {
		display: "flex",
		flexDirection: "column",
		alignItems: "center",
	},
});

const Signup = () => {
	const [formSignValues, handleSignInputChange] = useForm();
	const dispatch = useDispatch();

	const auth = useSelector((state) => state.auth);

	const classes = useStyles();

	const handleSign = (e) => {
		e.preventDefault();
		dispatch(
			signAccount(
				formSignValues.sFirstName,
				formSignValues.sLastName,
				formSignValues.sEmail,
				formSignValues.sPassword
			)
		);
	};

	const history = useHistory();

	return (
		<Box
			style={{ height: "100vh" }}
			bgcolor="primary.main"
			justifyContent="center"
			alignItems="center"
			display="flex"
		>
			<Paper className={classes.paperContainer_signup}>
				<Button
					variant="contained"
					color="secondary"
					onClick={() => {
						dispatch(logout());
						history.push("/login");
					}}
				>
					Go back
				</Button>
				<br />
				<br />

				<Typography id="transition-modal-title" variant="h5">
					Sign up
				</Typography>

				<form className={classes.formClass} onSubmit={handleSign}>
					<TextField
						autoComplete="off"
						margin="normal"
						id="outlined-basic"
						label="First Name"
						variant="outlined"
						name="sFirstName"
						onChange={handleSignInputChange}
						required
					/>
					<TextField
						autoComplete="off"
						margin="normal"
						id="outlined-basic"
						label="Last Name"
						variant="outlined"
						name="sLastName"
						onChange={handleSignInputChange}
						required
					/>

					<TextField
						autoComplete="off"
						margin="normal"
						id="outlined-basic"
						label="Email"
						variant="outlined"
						name="sEmail"
						onChange={handleSignInputChange}
						type="Email"
						required
					/>

					<TextField
						autoComplete="off"
						margin="normal"
						id="outlined-basic"
						label="Password"
						variant="outlined"
						name="sPassword"
						onChange={handleSignInputChange}
						required
					/>
					<br />
					{auth.error !== null
						? auth.error.map((err, i) => {
								return (
									<Alert severity="error" key={i}>
										{err.msg}
									</Alert>
								);
						  })
						: null}
					{auth.message !== null ? (
						<Alert severity="success">Cuenta Creada!</Alert>
					) : null}
					<br />
					<Button type="submit" variant="contained" color="primary">
						Crear cuenta
					</Button>
				</form>
			</Paper>
			{/* <Alert severity="success">Cuenta creada!</Alert> */}
		</Box>
	);
};

export default Signup;
