import { Box, Button, makeStyles, TextField, Typography } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { crearPost } from "../actions/posts";
import { fetchSinToken } from "../helpers/fetch";
import { useForm } from "../hooks/useForm";

const useStyles = makeStyles({
	fieldSize: {
		width: 250,
	},
});

function CreatePost() {
	const experience = [
		{
			value: "Senior",
			label: "Senior",
		},
		{
			value: "Semi-Senior",
			label: "Semi-Senior",
		},
		{
			value: "Junior",
			label: "Junior",
		},
	];

	const [categorias, setcategorias] = useState(null);
	const classes = useStyles();

	useEffect(() => {
		fetchSinToken("api/categorias")
			.then((res) => res.json())
			.then((res) => setcategorias(res));
	}, []);

	const [formValues, handleInputChange] = useForm({
		Category: "Back-End",
		ExperienceLabel: "Senior,",
	});

	const dispatch = useDispatch();
	const auth = useSelector((state) => state.auth);

	const endpoint = `api/addpost/${auth.uid}`;

	const handleSubmit = (e) => {
		e.preventDefault();
		console.log(formValues);
		dispatch(
			crearPost(
				endpoint,
				auth.uid,
				formValues.Category,
				formValues.jobTitleLabel,
				formValues.descriptionLabel,
				formValues.ExperienceLabel
			)
		);
	};

	return (
		<div>
			<Typography align="center" gutterBottom variant="h2" color="initial">
				Crear Oferta Laboral
			</Typography>

			<Box
				display="flex"
				justifyContent="center"
				textAlign="center"
				className="animate__animated animate__fadeIn animate__faster"
			>
				<form onSubmit={handleSubmit}>
					<TextField
						autoComplete="off"
						className={classes.fieldSize}
						margin="normal"
						id="outlined-basic"
						label="Título de Oferta"
						variant="outlined"
						name="jobTitleLabel"
						onChange={handleInputChange}
						required
					/>
					<br />
					<br />
					{categorias !== null ? (
						<TextField
							className={classes.fieldSize}
							id="outlined-select-currency-native"
							select
							label="Categoría"
							onChange={handleInputChange}
							name="Category"
							SelectProps={{
								native: true,
							}}
							variant="outlined"
							required
						>
							{categorias.items.map((option) => (
								<option key={option.categorie_name} value={option.categorie_name}>
									{option.categorie_name}
								</option>
							))}
						</TextField>
					) : null}
					<br />
					<TextField
						autoComplete="off"
						className={classes.fieldSize}
						margin="normal"
						id="outlined-multiline-static"
						label="Descripción de la Oferta"
						variant="outlined"
						name="descriptionLabel"
						onChange={handleInputChange}
						required
						multiline
						rows={5}
					/>

					<br />
					<br />
					<TextField
						className={classes.fieldSize}
						id="outlined-select-currency-native"
						select
						label="Experiencia"
						name="ExperienceLabel"
						onChange={handleInputChange}
						SelectProps={{
							native: true,
						}}
						variant="outlined"
						required
					>
						{experience.map((option) => (
							<option key={option.value} value={option.value}>
								{option.label}
							</option>
						))}
					</TextField>
					<br />
					<br />

					<br />

					<br />

					<Button type="submit" variant="contained" color="secondary">
						crear post
					</Button>
				</form>
			</Box>
		</div>
	);
}

export default CreatePost;
