const initialState = {
	checking: true,
	message: null,
	error: null,
};

export const authReducer = (state = initialState, action) => {
	switch (action.type) {
		case "AUTH_LOGIN":
			return {
				...state,
				...action.payload,
				checking: false,
				message: null,
				error: null,
			};

		case "AUTH_LOGOUT":
			return {
				checking: false,
				message: null,
				error: null,
			};

		case "AUTH_ERROR":
			return {
				message: action.payload,
			};

		case "SIGN_MESSAGE":
			return {
				message: action.payload,
				error: null,
			};
		case "SIGN_ERROR":
			return {
				error: action.payload,
				message: null,
			};
		case "CHECKING_FINISH":
			return {
				...state,
				checking: false,
			};
		default:
			return state;
	}
};
