import React, { useEffect, useState } from "react";
import { fetchConToken } from "../helpers/fetch";
import CardComponent from "./CardComponent";
import Typography from "@material-ui/core/Typography";
import { Box } from "@material-ui/core";

const PostsLists = () => {
	const [data, setdata] = useState(null);
	const [borrarRefresh, setborrarRefresh] = useState();

	useEffect(() => {
		fetchConToken("api/posts/")
			.then((res) => res.json())
			.then((res) => setdata(res));
	}, [borrarRefresh]);

	return (
		<div>
			<Typography align="center" gutterBottom variant="h2" color="initial">
				Lista de Ofertas
			</Typography>
			<Box display="flex" justifyContent="center" flexWrap="wrap">
				{data !== null ? (
					data.items.reverse().map((item, i) => {
						return (
							<Box
								key={i}
								m={3}
								className="animate__animated animate__fadeIn animate__faster"
							>
								<CardComponent
									setborrarRefresh={setborrarRefresh}
									type="post"
									id={item._id}
									description={item.description}
									createdAt={item.created_at.split("T")[0]}
									jobTitle={item.job_title}
									firstName={item.user.first_name}
									lastName={item.user.last_name}
									email={item.user.email}
									requirements={item.requirements.join(", ")}
								/>
							</Box>
						);
					})
				) : (
					<h3>Loading...</h3>
				)}
			</Box>
		</div>
	);
};

export default PostsLists;
