import {
	Divider,
	ListItem,
	ListItemIcon,
	ListItemText,
	makeStyles,
} from "@material-ui/core";
import List from "@material-ui/core/List";
import React from "react";
import { Link } from "react-router-dom";

import PostAddIcon from "@material-ui/icons/PostAdd";
import WorkIcon from "@material-ui/icons/Work";
import PeopleIcon from "@material-ui/icons/People";
import { useSelector } from "react-redux";

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
	toolbar: theme.mixins.toolbar,
	drawerPaper: {
		width: drawerWidth,
	},
}));

const DrawerList = () => {
	const classes = useStyles();

	const auth = useSelector((state) => state.auth);

	return (
		<div>
			<div className={classes.toolbar} />
			<Divider />
			<List>
				<ListItem button key="PostLists" component={Link} to="/posts">
					<ListItemIcon>
						<PostAddIcon />
					</ListItemIcon>
					<ListItemText primary="Lista de Ofertas" />
				</ListItem>

				<ListItem button key="RequestsLists" component={Link} to="/requests">
					<ListItemIcon>
						<WorkIcon />
					</ListItemIcon>
					<ListItemText primary="Solicitudes" />
				</ListItem>
			</List>
			<Divider />
			<List>
				<ListItem button key="CreatePost" component={Link} to="/createpost">
					<ListItemIcon>
						<PostAddIcon />
					</ListItemIcon>
					<ListItemText primary="Crear Oferta" />
				</ListItem>
				<ListItem button key="CreateRequest" component={Link} to="/createrequest">
					<ListItemIcon>
						<PostAddIcon />
					</ListItemIcon>
					<ListItemText primary="Crear Solicitud" />
				</ListItem>
			</List>
			<Divider />
			{auth.role === "ADMIN_ROLE" ? (
				<List>
					<ListItem button key="GetUsers" component={Link} to="/getusers">
						<ListItemIcon>
							<PeopleIcon />
						</ListItemIcon>
						<ListItemText primary="Lista de Usuarios" />
					</ListItem>
				</List>
			) : null}
		</div>
	);
};

export default DrawerList;
